.footer {
    background-color: var(--container-color);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.footer__container {
    padding: 2rem 0 2rem;
    
}

.footer__title,
.footer__link {
    color: var(--title-color);
}

.footer__title {
    text-align: center;
    margin-bottom: var(--mb-2);
}

.footer__link:hover {
    color: var(--title-color-dark);
}

.footer__list {
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
}

.footer__social {
    display: flex;
    justify-content: center;
    column-gap: 1.125rem;
}

.footer__social-link {
    background-color: var(--title-color);
    color: var(--container-color);
    font-size: 1.125rem;
    padding: 0.4rem;
    border-radius: 0.5rem;
    display: inline-flex;
}

.footer__social-link:hover {
    background-color: var(--title-color-dark);
}

.footer__copy {
    display: block;
    margin-top: 2rem;
    color: var(--title-color);
    text-align: center;
    font-size: var(--smaller-font-size);

}   


/* ================= BREAKPOINTS ================= */

/* For large devices */
@media screen and (max-width: 992px) {

    .footer__social-link{
        padding: 0.25rem;
        border-radius: 0.25rem;
        font-size: 1rem;
    }
   
     }
/* For medium devices */

@media screen and (max-width: 768px) {
    .footer__container {
        padding: 2rem 0 4rem;
    }

    .footer__list {
        column-gap: 1.5rem;
    }

}

/* For small devices */

@media screen and (max-width: 576px) {
    

    .footer__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        margin-left: 2rem;
        row-gap: 1rem;
    }
    .footer__container {
        padding: 2rem 0 3rem;
        margin-bottom: -100px;
    }




}
   